<template>
  <!--类别相同，内容不同的页面，使用同一个vue，不同的router名称跳转到相同的vue，并通过router名称判断显示内容-->
  <Row class="fullWidth">
    <iCol span="1" class="leftPanel left-background">
      <Row style="padding: 7px;" class="left-top">
        <span style="font-size: 16px;">{{ currentOrganization.name }}</span>
      </Row>
      <Menu id="panel-menu" ref="detail_menu" class="left-menu" :activeName="activeName"
            :openNames="openNames" accordion width="auto" @on-select="onNaviMenuClick">
        <Submenu name="report">
          <template slot="title">
            <span>安全指数综合评估</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="safety_index">云盾安全指数报告</Menu-item>
        </Submenu>
        <Submenu name="asset">
          <template slot="title">
            <span>资产状态统计分析</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="availability">完好率分析报告</Menu-item>
          <Menu-item name="fault_rate">故障率分析报告</Menu-item>
        </Submenu>
        <Submenu name="stability">
          <template slot="title">
            <span>消防系统稳定性分析</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <!--<Menu-item name="tendency">趋势分析</Menu-item>-->
          <Menu-item name="event_fire">火警事件分析报告</Menu-item>
          <Menu-item name="event_fault">故障事件分析报告</Menu-item>
          <Menu-item name="event_warn">告警事件分析报告</Menu-item>
          <Menu-item name="event_shield">屏蔽事件分析报告</Menu-item>
          <Menu-item name="event_start">启动事件分析报告</Menu-item>
          <Menu-item name="event_feedback">反馈事件分析报告</Menu-item>
        </Submenu>
        <!--<Submenu name="maintenance">-->
          <!--<template slot="title">-->
            <!--<span>维保工作</span>-->
          <!--</template>-->
          <!--<Menu-item name="weekly">维保周报</Menu-item>-->
          <!--<Menu-item name="monthly">维保月报</Menu-item>-->
        <!--</Submenu>-->
      </Menu>
      <!--</div>-->

    </iCol>
    <iCol class="rightPanel">
      <router-view @changeActiveName="changeActiveName"></router-view>
    </iCol>
  </Row>
</template>

<script>
  import config from '../../config/config';
  import server from '../../config/api';
  import niceScroll from 'jquery.nicescroll';
  import Vue from 'vue';


  export default {
    data() {
      return {
        activeName: 'safety_index',
        openNames: ['report','asset','maintenance'],
        currentOrganization: {}
      };
    },
    methods: {
      fetchData: function () {
        document.title = '单位管理 - ' + config.web_name;
        this.$store.commit('setActiveNaviMenu', 'report');
      },
      onNaviMenuClick: function (key) {
        //Route to sub module
        this.$router.push(key);
        $("#panel-div").niceScroll();
      },
      getReport:function () {
        alert("wo lai le");
      },
      changeActiveName: function(subPageName, pageName) {
        this.openNames = subPageName;
        this.activeName = pageName;
        this.$nextTick(() => {
          this.$refs.detail_menu.updateOpened();
        });
      }
    },
    mounted() {
      // 是否是从建筑物主页跳转进来的
      let orgId = undefined;
      let page = this.topYunSession('route_list');
      if(page === null) {
        // 无参数，显示默认监控中心数据
        this.$set(this.currentOrganization, 'name', localStorage.current_org_name);
        return;
      }
      orgId = page.orgId;
      this.$http.get(server.api.organization.baseInfo.get.replace('{orgId}', orgId) + "?orgClass=NORMAL_ENTITY").then((response) => {
        this.currentOrganization = response.body.data;
      }, (error) => {
        this.$Message.error('单位管理基本信息获取失败!');
      });

      let self = this;
      self.fetchData();
      Vue.nextTick(() => {
//        $("#panel-div").scrollbar();
//        setTimeout( () => {
        $("#panel-div").niceScroll();
//        }, 2000);
      })
    }

  }
</script>



